import React, { useRef, useEffect, useState } from 'react';
import { createPortal } from 'react-dom';

import useSourceStyles from 'hooks/useSourceStyles';


const WebcamSource = props => {
  const { sourceWidth, sourceHeight, canvasRef, videoSourceRef } = props;
  const videoRef = useRef();
  const [isReady, setIsReady] = useState(false);
  const sourceStyles = useSourceStyles(isReady, videoRef);

  console.log(sourceStyles);

  useEffect(() => {
    let isMounted = true;
    const clickHandler = () => {
      videoRef.current.play();
    };

    const userMediaConstraints = {
      audio: false,
      video: {
        facingMode: 'environment',
        width: {
          //ideal: sourceWidth,
          min: 640,
          max: 1920
        },
        height: {
          //ideal: sourceHeight,
          min: 480,
          max: 1080
        }
      }
    };

    navigator.mediaDevices.getUserMedia(userMediaConstraints)
      .then(stream => {
        if (isMounted) {
          videoRef.current.srcObject = stream;
          document.body.addEventListener('click', clickHandler);

          const readyInterval = setInterval(() => {
            if (videoRef.current && videoRef.current.videoWidth) {
              setIsReady(true);
              clearInterval(readyInterval);
            }
          }, 20);
        }
    });

    return () => {
      isMounted = false;
      document.body.removeEventListener('click', clickHandler);
    };
  }, [sourceWidth, sourceHeight]);

  return createPortal(
    <>
      <video
        muted
        autoPlay
        playsInline
        ref={node => {
          videoRef.current = node;
          videoSourceRef.current = node;
        }}
        style={sourceStyles}
      />
      <canvas
        ref={canvasRef}
        width={sourceWidth}
        height={sourceHeight}
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          ...sourceStyles
        }}
      />
    </>
  , document.getElementById('ar-container'));
};

WebcamSource.defaultProps = {
  sourceWidth: 640,
  sourceHeight: 480,
  canvasRef: {},
  videoSourceRef: {}
};

export default WebcamSource;
