window.THREEx = window.THREEx || {};

const { THREEx } = window;

THREEx.ArToolkitSource = function(parameters) {	
	this.ready = false;
  this.domElement = null;

	this.parameters = {
		sourceWidth: 640,
		sourceHeight: 480,
		displayWidth: 640,
		displayHeight: 480,
		...parameters
	}
}

THREEx.ArToolkitSource.prototype.dispose = function(){
	var _this = this;

	document.getElementById('ar-container').removeChild(_this.domElement);
};

THREEx.ArToolkitSource.prototype.init = function(onReady, onError){
	var _this = this;

	function onSourceReady() {
		document.getElementById('ar-container').appendChild(_this.domElement);
		_this.ready = true;
		onReady && onReady();
	}

	this.domElement = this._initSourceWebcam(onSourceReady, onError);
	this.domElement.style.position = 'absolute';
	this.domElement.style.top = '0px';
	this.domElement.style.left = '0px';
	this.domElement.style.zIndex = '-2';
	return this;
}


////////////////////////////////////////////////////////////////////////////////
//          handle webcam source
////////////////////////////////////////////////////////////////////////////////

THREEx.ArToolkitSource.prototype._initSourceWebcam = function(onReady, onError) {
	// var _this = this

	// init default value
	onError = onError || function(error){	
		alert('Webcam Error\nName: '+error.name + '\nMessage: '+error.message)
	}

	var domElement = document.createElement('video');
	domElement.setAttribute('autoplay', '');
	domElement.setAttribute('muted', '');
	domElement.setAttribute('playsinline', '');
	domElement.style.width = this.parameters.displayWidth+'px'
	domElement.style.height = this.parameters.displayHeight+'px'

	// get available devices
	navigator.mediaDevices.enumerateDevices().then(function(devices) {
		var userMediaConstraints = {
			audio: false,
			video: {
				facingMode: 'environment',
				width: {
					// ideal: _this.parameters.sourceWidth,
					min: 640,
					max: 1920
				},
				height: {
					// ideal: _this.parameters.sourceHeight,
					min: 480,
					max: 1080
				}
			}
		}
		// get a device which satisfy the constraints
		navigator.mediaDevices.getUserMedia(userMediaConstraints).then(function success(stream) {
			// set the .src of the domElement
			domElement.srcObject = stream;
			document.body.addEventListener('click', () => domElement.play())

			var interval = setInterval(() => {
				if (domElement.videoWidth) {
					onReady()
					clearInterval(interval);
				}
			}, 20);
		}).catch(function(error) {
			onError({
				name: error.name,
				message: error.message
			});
		});
	}).catch(function(error) {
		onError({
			message: error.message
		});
	});

	return domElement
}

////////////////////////////////////////////////////////////////////////////////
//          handle resize
////////////////////////////////////////////////////////////////////////////////

THREEx.ArToolkitSource.prototype.onResizeElement = function(){
	var screenWidth = window.innerWidth;
	var screenHeight = window.innerHeight;
	var sourceWidth = this.domElement.videoWidth;
	var sourceHeight = this.domElement.videoHeight;
	// compute sourceAspect
	var sourceAspect = sourceWidth / sourceHeight;
	// compute screenAspect
	var screenAspect = screenWidth / screenHeight;

	// if screenAspect < sourceAspect, then change the width, else change the height
	if(screenAspect < sourceAspect) {
		// compute newWidth and set .width/.marginLeft
		var newWidth = sourceAspect * screenHeight
		this.domElement.style.width = newWidth+'px'
		this.domElement.style.marginLeft = -(newWidth-screenWidth)/2+'px'
		
		// init style.height/.marginTop to normal value
		this.domElement.style.height = screenHeight+'px'
		this.domElement.style.marginTop = '0px'
	}else{
		// compute newHeight and set .height/.marginTop
		var newHeight = 1 / (sourceAspect / screenWidth)
		this.domElement.style.height = newHeight+'px'
		this.domElement.style.marginTop = -(newHeight-screenHeight)/2+'px'
		
		// init style.width/.marginLeft to normal value
		this.domElement.style.width = screenWidth+'px'
		this.domElement.style.marginLeft = '0px'
	}
}

THREEx.ArToolkitSource.prototype.copyElementSizeTo = function(otherElement){
	otherElement.style.width = this.domElement.style.width
	otherElement.style.height = this.domElement.style.height
	otherElement.style.marginLeft = this.domElement.style.marginLeft
	otherElement.style.marginTop = this.domElement.style.marginTop
}
