import Hammer from 'hammerjs';
import React, { useEffect, useRef, useState } from 'react';
import { Scene, Camera, WebGLRenderer, Group, Color, PointLight, AmbientLight, AnimationMixer, Clock } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

import SoundWave from 'components/SoundWave';
import SoundIcon from 'components/SoundIcon';
import WebcamSource from 'components/WebcamSource';

import './AR.css';

const AR = props => {
  const { initialScale, patternUrl, cameraParametersUrl, modelUrl, audioUrl } = props;
  const audioRef = useRef();
  const modelRef = useRef();
  const hammerRef = useRef();
  const canvasRef = useRef();
  const videoSourceRef = useRef();
  const pinchStartScale = useRef();
  const panStartRotation = useRef({ eventRotation: 0, modelRotation: 0 });
  const [isMute, setIsMute] = useState(false);
  const [isMarkerFound, setIsMarkerFound] = useState(false);


  useEffect(() => {
    if (isMarkerFound) {
      audioRef.current.play();
    } else {
      audioRef.current.pause();
    }
  }, [isMarkerFound])

  useEffect(() => {
    const onPinchStart = e => {
      if (modelRef.current) {
        pinchStartScale.current = modelRef.current.scene.scale.x;
      }
    };

    const onRotateStart = e => {
      if (modelRef.current) {
        panStartRotation.current = {
          modelRotation: modelRef.current.scene.rotation.y,
          eventRotation: e.rotation
        };
      }
    };

    const MIN_SCALE = initialScale / 3;
    const MAX_SCALE = initialScale * 3;

    hammerRef.current = new Hammer(document.getElementById('ar-container'));
    hammerRef.current.get('pan').set({ enable: false });
    hammerRef.current.get('pinch').set({ enable: true });
    hammerRef.current.get('rotate').set({ enable: true });
    hammerRef.current.on('pinchstart', onPinchStart);
    hammerRef.current.on('pinch', e => {
      if (pinchStartScale.current && modelRef.current) {
        const scale = pinchStartScale.current * e.scale;
        const boundedScale = Math.min(Math.max(scale, MIN_SCALE), MAX_SCALE);

        modelRef.current.scene.scale.set(boundedScale, boundedScale, boundedScale);
      }
    });
    hammerRef.current.on('rotatestart', onRotateStart);
    hammerRef.current.on('rotate', e => {
      if (modelRef.current) {
        const deltaRotationAngle = e.rotation - panStartRotation.current.eventRotation;
        const deltaRotation = deltaRotationAngle / 180 * Math.PI;

        modelRef.current.scene.rotation.y = panStartRotation.current.modelRotation - deltaRotation;
      }
    });

    return () => {
      hammerRef.current.destroy();
    }
  }, [initialScale])

  useEffect(() => {
    let mixer;
    let mixerAction;
    let deltaTime = 0;
    let isMounted = true;
    let isMarkerVisible = false;
    const { width, height } = { width: 640, height: 480 };
    const { ArToolkitContext, ArMarkerControls } = window.THREEx;
    const scene = new Scene();
    const clock = new Clock();
    const camera = new Camera();
    const renderer = new WebGLRenderer({
      canvas: canvasRef.current,
      antialias: true,
      alpha: true
    });
    const arToolkitContext = new ArToolkitContext({
      cameraParametersUrl,
      detectionMode: 'mono',
      canvasWidth: width,
      canvasHeight: height
    });
    const markerRoot = new Group();
  
    scene.add(camera);
    renderer.setClearColor(new Color('lightgrey'), 0)
    renderer.setPixelRatio(window.devicePixelRatio);

    arToolkitContext.init(() => {
      camera.projectionMatrix.copy(arToolkitContext.getProjectionMatrix());
    });

    new ArMarkerControls(arToolkitContext, markerRoot, {
      type: 'pattern',
      patternUrl
    })

    new GLTFLoader()
      .load(modelUrl, gltf => {
        modelRef.current = gltf;
        const modelScene = gltf.scene;

        modelScene.scale.set(initialScale, initialScale, initialScale);

        var light3 = new AmbientLight(0xffffff, 1);
        markerRoot.add(light3);
  
        var light4 = new PointLight(0xffffff, 1, 0, 2);
        light4.position.set(0, 1, 1);
        markerRoot.add(light4);

        var light5 = new PointLight(0xffffff, 1, 0, 2);
        light5.position.set(0, 1, -1);
        markerRoot.add(light5);

        markerRoot.add(modelScene);

        mixer = new AnimationMixer(modelScene);
        const clip = gltf.animations[0];

        mixerAction = mixer.clipAction(clip);
        mixerAction.play();
        mixerAction.paused = !isMarkerVisible;
        
        scene.add(markerRoot);
      });

    const render = () => {
      renderer.render(scene, camera);
    };

    const update = () => {
      if (isMarkerVisible !== markerRoot.visible) {
        isMarkerVisible = markerRoot.visible;
        setIsMarkerFound(markerRoot.visible);

        if (mixerAction) {
          mixerAction.paused = !isMarkerVisible;
        }
      }

      if (mixer) {
        mixer.update(deltaTime);
      }

      if (videoSourceRef.current) {
        arToolkitContext.update(videoSourceRef.current);
      }
    };
    
    
    const animate = () => {
      if (isMounted) {
        requestAnimationFrame(animate);
        deltaTime = clock.getDelta();
        update();
        render();
      }
    };

    animate();

    return () => {
      isMounted = false;
    };
  }, [initialScale, cameraParametersUrl, patternUrl, modelUrl]);

  return (
    <div>
        <WebcamSource
          canvasRef={canvasRef}
          videoSourceRef={videoSourceRef}
        />
        <div className='ar-icons-overlay' />
        <audio
          loop
          ref={audioRef}
          preload='auto'
        >
          <source
            src={audioUrl}
            type='audio/mpeg'
          />
        </audio>
        <button
          className='ar-icon ar-icon-sound'
          onClick={() => {
            setIsMute(!isMute);
            audioRef.current.volume = isMute ? 1 : 0;
          }}
        >
          <SoundIcon
            width={32}
            height={32}
            isMute={isMute}
          />
        </button>
        <SoundWave
          width={32}
          height={32}
          isActive={isMarkerFound}
          className='ar-icon ar-icon-wave'
        />
    </div>
  );
};

AR.defaultProps = {
  initialScale: 0.0005,
  audioUrl: `${process.env.PUBLIC_URL}/audio/anchor.mp3`,
  modelUrl: `${process.env.PUBLIC_URL}/models/anchor/MiraiKoukai7.gltf`,
  patternUrl: `${process.env.PUBLIC_URL}/data/hiro.patt`,
  cameraParametersUrl: `${process.env.PUBLIC_URL}/data/camera_para.dat`
};

export default AR;
