import useWindowSize from './useWindowSize';

const useSourceStyles = (isReady, videoRef) => {
  const { width, height } = useWindowSize();

  if (!isReady) {
    return {
      width,
      height,
      marginTop: 0,
      marginLeft: 0
    };
  }

  const { videoWidth, videoHeight } = videoRef.current;
  const sourceWidth = Math.max(videoWidth, videoHeight);
  const sourceHeight = Math.min(videoWidth, videoHeight);

  const sourceAspect = sourceWidth / sourceHeight;
  const screenAspect = width / height;

  console.log(sourceWidth, sourceHeight, screenAspect, sourceAspect);

  if (screenAspect < sourceAspect) {
    const newWidth = sourceAspect * height;

    return {
      height,
      width: newWidth,
      marginTop: 0,
      marginLeft: -(newWidth - width) / 2
    };
  } else {
    const newHeight = 1 / (sourceAspect / width);

    return {
      width,
      height: newHeight,
      marginTop: -(newHeight - height) / 2,
      marginLeft: 0
    };
  }
};

export default useSourceStyles;
